:root {
  --primary-color: #66DC89;
  --secondary-color: #AB54A9;
  --ternary-color: #FA9E15;
  --primary-alt: #B2FFC9;
  --secondary-alt: #D699D5;
  --ternary-alt: #FFCC80;
  --primary-alt-opaque: #B2FFC980;
  --secondary-alt-opaque: #D699D580;
  --ternary-alt-opaque: #FFCC8080;
}

* {
  box-sizing: border-box;
}

#root {
  max-width: 100vw;
  overflow: hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  width: 200px;
  height: 200px;
}

a {
  text-decoration: none;
}

.section_body-heading {
  width: 100%;
  margin-top: 0px;
  font-size: 4rem;
  font-weight: 400;
  text-align: center;
}

.full-width_copy {
  width: 100%;
  font-size: 1.5em;
  margin-top: 0px;
}

.half-width_copy {
  min-width: 100%;
  margin-bottom: 40px;
  font-size: 1.5em;
  margin-top: 0px;
}

.section_body-middle {
  margin: 80px 0px;
  padding: 0px;
  display: flex;
}

.section_body-middle_text {
  min-width: 50%;
  padding: 0px 5vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.section_body-middle-image-container {
  min-width: 50%;
  display: flex;
  justify-content: center;
  margin: auto;
}

.section_image-wrapper {
  z-index: -1;
  padding: 10px;
}

.section_middle-image {
  margin-top: 18%;
  min-width: 100%;
  height: auto;
}

.about-info_section_middle-image {
  margin-top: 26.5%;
  min-width: 100%;
  height: auto;
}

